.button{
    background-color: rgba(23, 26, 32, 0.8);
    height: 48px;
    width: 254px;
    margin: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    opacity: 0.85;
}

.button>a{
    text-decoration: none;
    color: white;
    font-size: 12px;
    text-align: center;
}

.button__white{
    background-color: white;
    opacity: 0.65;
}

.button__white>a{
    color:black;
}